import { Icon } from '@iconify/react';

import useLocales from '../../hooks/useLocales';

import ResetConfirmModal from '../../sections/ticket/ResetConfirmModal';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function BillingAddress() {
    const { t } = useLocales();

    return (
        <Page>
            <PageBanner>
                <div className="container p-4 mb-8">
                    <div className="flex flex-col gap-8 justify-center items-center">
                        <div className="flex flex-col gap-4 w-full">
                            <h3 className="text-[64px]">{t('title.billing_address')}</h3>
                            <p className="text-stone-400 w-1/2">{t('description.billing_address')}</p>
                        </div>

                        <div className="w-full mb-8 mt-8 flex flex-col gap-4">
                            <GradientBorderWrapper>
                                <div className="flex sm:flex-col gap-1 justify-start p-6">
                                    <h3 className="text-[28px]">Rua Jacques Félix, 626 (Ap 1001)</h3>
                                    <p className="text-stone-400">
                                        Vila Nova Conceição, São Paulo/SP - CEP: 04509002 - Tel: (11) 98555-8800
                                    </p>
                                    <span className="text-primary underline cursor-pointer text-sm mt-3">
                                        {`${t('action.change_address')}`}
                                    </span>
                                </div>
                            </GradientBorderWrapper>
                        </div>

                        <div className="w-full flex flex-col gap-8">
                            <div className="flex flex-col gap-4 ">
                                <h3 className="text-[28px] w-1/2">{t('title.purchase_summary')}</h3>
                                <div className='flex w-1/5' >
                                    <GradientBorderWrapper className="event-active-bg rounded-2xl">
                                        <div className="flex w-full">
                                            <button className="btn btn-secondary p-4 gap-4 rounded-2xl lowercase h-auto">
                                                <p >{`1 ${t('title.ticket')}`}</p>
                                                <span className="text-primary underline cursor-pointer capitalize text-sm font-normal">
                                                    {t('action.change_ticket')}
                                                </span>
                                            </button>
                                        </div>
                                    </GradientBorderWrapper>
                                </div>
                            </div>
                            <GradientBorderWrapper>
                                {/* ticket */}
                                <div className="flex gap-2 justify-between py-4 px-6 items-center">
                                    <div className="flex flex-col gap-1">
                                        <p className="font-bold">{t('title.selected_tickets')}</p>
                                    </div>
                                    <div className="flex gap-[88px] justify-between items-center capitalize">
                                        <p className="flex flex-col items-center font-bold w-26">{t('title.amount')}</p>
                                        <p className="flex flex-col items-center font-bold w-26">{t('title.price')}</p>
                                    </div>
                                </div>
                                <div className="divider m-0 h-1" />
                                {/* ticket detail */}
                                <div className="flex gap-2 justify-between py-4 px-6 items-center">
                                    <div className="flex flex-col gap-1">
                                        <p className="text-stone-200">2º Lote</p>
                                        <p className="text-stone-400">Meia Social - Unissex</p>
                                        <p className="text-stone-300 text-sm">Sáb. 09/04 - 16:00</p>
                                    </div>
                                    <div className="flex gap-[88px] justify-between capitalize">
                                        <div className="flex flex-col items-center text-base w-26  text-stone-200">
                                            1
                                        </div>
                                        <div className="flex flex-col items-end w-26">
                                            <p className="text-stone-200">R$ 210,00</p>
                                            <p className="text-stone-400">Taxa: R$21,00</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="divider h-1 m-0" />
                                {/* sell price */}
                                <div className="flex gap-2 justify-between py-4 px-6 items-center">
                                    <div className="flex flex-col gap-1">
                                        <p className="text-stone-400">Total em Ingressos</p>
                                        <p className="text-stone-400">Taxa de Serviço</p>
                                        <p className="text-stone-400">Total de Processamento</p>
                                        <h5 className="text-2xl text-stone-200">Total em Ingressos</h5>
                                    </div>
                                    <div className="flex gap-[88px] justify-between capitalize">
                                        <div className="flex flex-col items-center text-base w-26 text-stone-200" />
                                        <div className="flex flex-col items-end w-26">
                                            <p className="text-stone-400">R$210,00</p>
                                            <p className="text-stone-400">R$21,00</p>
                                            <p className="text-stone-400">R$5,20</p>
                                            <h5 className="text-2xl text-stone-200">R$236,20</h5>
                                        </div>
                                    </div>
                                </div>
                            </GradientBorderWrapper>
                        </div>

                        <div className="flex flex-col gap-8 justify-center items-center w-full">
                            <div className="w-full mb-8">
                                <div className="w-full flex justify-end gap-8">
                                    <button className="btn btn-primary btn-outline px-8 capitalize py-4 rounded-2xl text-[19px] h-auto">
                                        <Icon icon="ic:round-help-outline" className="text-primary w-6 h-6 mr-2" />
                                        {t('action.help')}
                                    </button>
                                    <button className="btn btn-primary btn-outline px-8 capitalize py-4 rounded-2xl text-[19px] h-auto">
                                        <Icon
                                            icon="material-symbols:cancel-outline"
                                            className="text-primary w-6 h-6 mr-2"
                                        />
                                        {t('action.close')}
                                    </button>
                                </div>
                            </div>
                        </div>
                        <ResetConfirmModal />
                    </div>
                </div>
            </PageBanner>
        </Page>
    );
}
